@import "../../common/styles/sass/variables";

.work {
  /*width: 274px;*/
  width: 33.3333%;
  padding-left: 15px;
  padding-right: 15px;

  /* work Item */
  .workItem {
    position: relative;
    display: block;
    background-color:$bgSecondColor;
    border: 1px solid #222324;
    margin-bottom: 50px;
    overflow: hidden;
    cursor: pointer;
    border: 1px solid rgba(255, 255, 255, 0.1);
    /* work Image */
    .workImage {
      & img {
        height: 200px;
        transition: all 1s ease;
        background-size: cover;
        &:hover {
          -webkit-transform: scale(1.2);
          -moz-transform: scale(1.2);
          -ms-transform: scale(1.2);
          -o-transform: scale(1.2);
          transform: scale(1.2);
        }
      }
    }

    /* work Content */
    .workContent {
      padding: 28px 16px 36px;
      text-align: center;
      width: 82%;
      position: relative;
      background-color: #222;
      margin-left: auto;
      margin-right: auto;
      margin-top: -40px;
      border: 1px solid rgba(255, 255, 255, 0.1) ;
      border-bottom: none;
      /* work Content Cat */
      .cat {
        color: $mainColor;
        margin-bottom: 7px;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 700;
        letter-spacing: 1.5px;
        display: inline-block;
      }

      /* work Content Title */
      .title {
        margin-bottom: 6px;
        font-size: 21px;
        letter-spacing: .5px;
        text-transform: capitalize;
      }

      /* work Content Description */
      .workDescription {
        font-family: serif;
        font-style: italic;
        color: $textColor;
        font-size: 14px;
      }
    }
  }

  @media screen and (max-width: 992px) {
    width: 50%;
  }
  @media screen and (max-width: 576px) {
    width: 100%;
  }
}
